import React, { useMemo } from "react";
import { PopupsFiltersProps } from "./types";
import ComponentStyled from "./styles";
import { PopupFilters } from "../PopupFilters";
import { useLangStrings } from "../../hocs/useLangStrings";
import { SetPopUpIsActive } from "../../layouts/PopUpLayout/types";

const Component: React.FC<PopupsFiltersProps> = ({
  data,
  formikFormsData,
  onPopupActiveStatusChange,
  ...restProps
}) => {
  const { translate } = useLangStrings();

  const PopUps = useMemo(
    () =>
      data.map((item, _) => {
        const formData = formikFormsData[item.name];
        const selectedFiltersCount = Object.values(formData.values).filter(
          (i: boolean) => i
        ).length;

        const handleIsActiveChange: SetPopUpIsActive = (newStatus) => {
          onPopupActiveStatusChange(item.name, newStatus);

          if (!newStatus) {
            formData.submitForm();
            return;
          }
        };

        return (
          <li key={`pop-up-filter-${item.name}`}>
            <PopupFilters
              formik={formData}
              selectedFiltersCount={selectedFiltersCount}
              title={translate(item.title)}
              isActive={item.isShow!!}
              setIsActive={handleIsActiveChange}
              data={item.filters}
              onCloseAndClearClick={item.onCloseAndClearClick}
              onContinueClick={() => {
                item.onContinueClick?.();
                handleIsActiveChange(false);
              }}
            />
          </li>
        );
      }),
    [data, formikFormsData, onPopupActiveStatusChange, translate]
  );

  return <ComponentStyled {...restProps}>{PopUps}</ComponentStyled>;
};

export default React.memo(Component);
