
import styled from 'styled-components';
import { TextAreaFieldProps, TextInputFieldProps } from './types';
import { containerWhiteRoundedStyle } from '../../styles/common';

const commonStyles = () => `
  ${containerWhiteRoundedStyle()}
  display: block;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  color: #212529;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  appearance: none;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  
  &:focus {
    color: #212529;
    border-color: #86b7fe;
    outline: 0;
    box-shadow: 0 0 0 .25rem rgba(13,110,253,.25);
  }
  
  &::placeholder {
    color: #6c757d;
  }

  @media (max-width: 1160px) {
    font-size: 14px;
  }
`;

const generateBackgroundColor: (readOnly?: boolean) => string =  (readOnly = false) => readOnly ? '#e9ecef' : '#fff';

export default styled.input<TextAreaFieldProps | TextInputFieldProps>`
  ${commonStyles}
  background-color: ${({readOnly}) => generateBackgroundColor(readOnly)};
`;

export const TextAreaStyled = styled.textarea<TextAreaFieldProps>`
  ${commonStyles}
  background-color: ${({readOnly}) => generateBackgroundColor(readOnly)};
  resize: none;
`;