import { HTMLAttributes } from "react";
import { FilterTextFieldName, NativeFiltersFormValues } from "../../types";
import { FormikProps } from "formik";

export interface FilterFieldsProps extends HTMLAttributes<HTMLDivElement> {
    formikFormData: FormikProps<NativeFiltersFormValues>,
}

export type FilterTextFieldNameWithoutTileAndCw = Exclude<FilterTextFieldName, 'tile' | 'cw'>;

export enum FilterTileFieldValues {
    Yes = 'Yes',
    No = 'No',
};
export type FilterTileFieldValue = keyof typeof FilterTileFieldValues;

export type FilterInputNameValues = {
    [key in FilterTextFieldNameWithoutTileAndCw]: string;
} & {
    tile: FilterTileFieldValue;
    cw: number;
};