import { useMemo } from "react";
import { useFormik } from "formik";

import { defaultNativeFilterTileSelectValue } from "../components/FilterFields/component";
import {
  UseNativeFiltersProps,
  NativeFiltersFormValues,
} from "../types";

const defaultFormikFormCwValue = 100;
export const nativeFilterCrefTagPrompt = "--cref";
export const nativeFiltersFormikFormCwInputName = "cw";
export const nativeFiltersFormikFormVersionUploadImageSupportsValue = 6;

export const useNativeFilters: UseNativeFiltersProps = ({
  initFormikValues,
  onFormikFormSubmit,
}) => {
  const defaultInitialValues: NativeFiltersFormValues = useMemo(
    () => ({
      aspect: "",
      version: "",
      quality: "",
      stylize: "",
      chaos: "",
      stop: "",
      repeat: "",
      weird: "",
      tile: defaultNativeFilterTileSelectValue,
      seed: "",
      no: "",
      cw: defaultFormikFormCwValue,
    }),
    []
  );

  const finalInitialValues = useMemo(
    () => ({
      ...defaultInitialValues,
      ...initFormikValues,
    }),
    [defaultInitialValues, initFormikValues]
  );

  const formikFormData = useFormik<NativeFiltersFormValues>({
    initialValues: finalInitialValues,
    onSubmit: (...props) => onFormikFormSubmit?.(...props),
  });

  const isUploadImageApprove = useMemo((): boolean => {
    const filterVersionValue = formikFormData.values.version;

    return (
      !!filterVersionValue.trim() &&
      !Number.isNaN(filterVersionValue) &&
      +filterVersionValue ===
        nativeFiltersFormikFormVersionUploadImageSupportsValue
    );
  }, [formikFormData]);

   
  return {
    formikFormData,
    isUploadImageApprove,
  };
};
