import React, { useMemo } from "react";
import { SliderFieldProps } from "./types";
import ComponentStyled, {
  BackgroundLineStyled,
  InputSliderStyled,
} from "./styles";

const defaultRangeStepValue = 0.1;

const Component: React.FC<SliderFieldProps> = ({
  value,
  stepValue = defaultRangeStepValue,
  minValue = 0,
  maxValue = 5,
  containerProps,
  ...restProps
}) => {
  const thumbStyle = useMemo(
    () => ({
      width: 16,
      height: 16,
      backgroundColor: "#0d6efd",
      boxShadow: "none",
    }),
    []
  );

  const thumbActiveStyle = useMemo(
    () => ({
      opacity: 0.5,
    }),
    []
  );

  const tackStyle = useMemo(
    () => ({
      backgroundColor: "transparent",
    }),
    []
  );

  return (
    <ComponentStyled {...containerProps}>
      <BackgroundLineStyled />

      <InputSliderStyled
        xstep={defaultRangeStepValue}
        xmin={minValue}
        xmax={maxValue}
        x={value}
        styles={{
          thumb: thumbStyle,
          active: thumbActiveStyle,
          track: tackStyle,
        }}
        {...restProps}
      />
    </ComponentStyled>
  );
};

export default React.memo(Component);
