import React, { useCallback, useMemo } from "react";
import { FormikProps } from "formik";

import { PopupFiltersProps } from "./types";
import { PopupFooterStyled } from "./styles";
import { useLangStrings } from "../../hocs/useLangStrings";
import { PopUpLayout } from "../../layouts/PopUpLayout";
import { FiltersGrid } from "../FiltersGrid";
import { Button } from "../Button";
import { ButtonLight } from "../ButtonLight";
import { LangStringKey } from "../../types/lang";

const Component = <FormFormikProps extends Record<string, boolean>>({
  data,
  title,
  isActive,
  setIsActive,
  FooterComponents,
  onCloseAndClearClick,
  onContinueClick,
  selectedFiltersCount,
  formik,
  ...restProps
}: PopupFiltersProps<FormFormikProps>) => {
  const { translate } = useLangStrings();

  const handleCloseAndClearClick = useCallback(() => {
    formik.resetForm();
    setIsActive(false);

    onCloseAndClearClick?.();
  }, [formik, setIsActive, onCloseAndClearClick]);

  const handleContinueClick = useCallback(() => {
    formik.submitForm();
    onContinueClick?.();
  }, [formik, onContinueClick]);

  const isContinueButtonDisabled = useMemo(
    (): boolean =>
      selectedFiltersCount !== undefined && selectedFiltersCount <= 0,
    [selectedFiltersCount]
  );

  const continueButtonText = useMemo(() => {
    let defaultVal = translate("buttonContinueTitle");

    if (isContinueButtonDisabled) {
      return defaultVal;
    }
    return `${defaultVal}(${selectedFiltersCount})`;
  }, [selectedFiltersCount, isContinueButtonDisabled, translate]);

  const clearButtonTranslateLangTextKey = useMemo(
    (): LangStringKey =>
      !isContinueButtonDisabled
        ? "buttonClearAllAndCloseTitle"
        : "buttonCancelTitle",
    [isContinueButtonDisabled]
  );

  return (
    <PopUpLayout
      title={title}
      isActive={isActive}
      setIsActive={setIsActive}
      FooterComponents={
        <PopupFooterStyled>
          {FooterComponents}

          <ButtonLight onClick={handleCloseAndClearClick}>
            {translate(clearButtonTranslateLangTextKey)}
          </ButtonLight>

          <Button
            withGreenColor
            type="submit"
            disabled={isContinueButtonDisabled}
            onClick={handleContinueClick}
          >
            {continueButtonText}
          </Button>
        </PopupFooterStyled>
      }
      {...restProps}
    >
      <form onSubmit={formik.handleSubmit}>
        <FiltersGrid
          data={data}
          formik={formik as unknown as FormikProps<Record<string, boolean>>}
        />
      </form>
    </PopUpLayout>
  );
};

export default React.memo(Component);
