import React from "react";
import { Tooltip } from "react-tooltip";

import { FilterInputLayoutProps } from "./types";
import ContainerStyled, {
  ContainerDescriptionStyled,
  IconInfoStyled,
  IconStyled,
} from "./styles";
import { useLangStrings } from "../../hocs/useLangStrings";

const Component: React.FC<FilterInputLayoutProps> = ({
  label,
  name,
  tooltipDescription,
  faIconClassName,
  style,
  children,
}) => {
  const { translate } = useLangStrings();
  const finalName = name ?? Date.now();

  return (
    <ContainerStyled style={style}>
      <ContainerDescriptionStyled>
        <IconStyled className={faIconClassName} />

        <span> {translate(label)} </span>

        <div className="App-tooltip" id={`tooltip-${finalName}`}>
          <IconInfoStyled className="fa-sharp fa-solid fa-circle-info" />

          <Tooltip
            anchorSelect={`#tooltip-${finalName}`}
            className="App-tooltip-content"
          >
            {translate(tooltipDescription)}
          </Tooltip>
        </div>
      </ContainerDescriptionStyled>

      {children}
    </ContainerStyled>
  );
};

export default React.memo(Component);
