import React, {
  forwardRef,
  useCallback,
  useEffect,
  useImperativeHandle,
  useMemo,
  useRef,
  useState,
} from "react";

import { UploadImageProps, UploadImageRefProps } from "./types";
import ComponentStyled, {
  ImageStyled,
  ImageContainerStyled,
  ImagePlaceholderStyled,
  ButtonStyled,
  ButtonLightenStyled,
  FilterSliderFieldStyled,
  ErrorStyled,
} from "./styles";
import {
  API_UPLOAD_IMAGE_SERVICE_ENDPOINT_URL,
  API_UPLOAD_IMAGE_SERVICE_KEY,
} from "../../utils";
import { SliderFieldProps } from "../SliderField/types";
import { useLangStrings } from "../../hocs/useLangStrings";

const MAX_FILE_SIZE_MB = 32;
const MAX_FILE_SIZE_BYTES = MAX_FILE_SIZE_MB * 1024 * 1024;

const Component = forwardRef<UploadImageRefProps, UploadImageProps>(
  (
    {
      formikFormData,
      onUploadClick,
      imageUrl,
      setUploadedImageUrl,
      onFilterSliderValueChange,
      ...restProps
    },
    ref
  ) => {
    const [selectedFile, setSelectedFile] = useState<File | null>(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const fileInputRef = useRef<HTMLInputElement | null>(null);

    const { translate } = useLangStrings();

    useImperativeHandle(ref, () => ({
      clearInputAndState() {
        setLoading(false);
        setSelectedFile(null);

        if (!fileInputRef.current) {
          return;
        }
        fileInputRef.current.value = "";
        setError(null);
      },
    }));

    const handleDeleteImageClick = useCallback(() => {
      setUploadedImageUrl("");
      setSelectedFile(null);
    }, [setUploadedImageUrl]);

    const handleUploadClick = useCallback(() => {
      if (!fileInputRef.current) {
        return;
      }
      fileInputRef.current.click();
    }, []);

    useEffect(() => {
      if (!selectedFile) {
        return;
      }
      if (selectedFile.size > MAX_FILE_SIZE_BYTES) {
        setError(
          `${translate(
            "buttonUploadImageSizeLimitError"
          )} ${MAX_FILE_SIZE_MB}Mb`
        );
        return;
      }

      const upload = async () => {
        setError(null);
        setLoading(true);

        try {
          const formData = new FormData();
          formData.append("UPLOADCARE_PUB_KEY", API_UPLOAD_IMAGE_SERVICE_KEY);
          formData.append("UPLOADCARE_STORE", "1");
          formData.append("file", selectedFile);

          const response = await fetch(
            `https://${API_UPLOAD_IMAGE_SERVICE_ENDPOINT_URL}`,
            {
              method: "POST",
              body: formData,
            }
          );

          if (!response.ok) {
            setError(translate("buttonUploadImageError"));
            return;
          }
          const data = await response.json();
          const imageUrl = `https://ucarecdn.com/${data.file}/`;
          setUploadedImageUrl(imageUrl);
        } catch (error: any) {
          console.log({ error });
          setError(error.message);
        } finally {
          setLoading(false);
        }
      };

      upload();
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selectedFile, setUploadedImageUrl]);

    const handleFileChange = useCallback(
      (event: React.ChangeEvent<HTMLInputElement>) => {
        if (!event.target.files || !event.target.files.length) {
          return;
        }
        setSelectedFile(event.target.files[0]);
      },
      []
    );

    const hasImageShowApprove = useMemo(
      (): boolean => !error && !!imageUrl.trim(),
      [error, imageUrl]
    );

    const inputSliderData = useMemo(
      (): SliderFieldProps => ({
        minValue: 0,
        maxValue: 100,
        stepValue: 1,
        value: +formikFormData.values.cw,
      }),
      [formikFormData]
    );

    const filterSliderCompliancePromptName = useMemo(
      (): string => translate("buttonUploadImageFilterComplianceTitle"),
      [translate]
    );

    return (
      <ComponentStyled {...restProps}>
        <input
          type="file"
          ref={fileInputRef}
          hidden
          accept="image/*"
          onChange={handleFileChange}
        />

        {hasImageShowApprove && (
          <>
            <ImageContainerStyled>
              <ImageStyled alt="Uploaded Image" src={imageUrl} />

              <ImagePlaceholderStyled>
                <ButtonStyled
                  isDelete
                  withRedColor
                  onClick={handleDeleteImageClick}
                >
                  X
                </ButtonStyled>
              </ImagePlaceholderStyled>
            </ImageContainerStyled>

            <FilterSliderFieldStyled
              sliderInputData={inputSliderData}
              onValueChange={onFilterSliderValueChange}
              promptName={filterSliderCompliancePromptName}
            />
          </>
        )}

        <ButtonLightenStyled
          withMt={!!selectedFile}
          onClick={handleUploadClick}
          disabled={loading}
        >
          <i className="fa-regular fa-upload" />{" "}
          {loading
            ? translate("buttonUploadImageLoadingTitle")
            : !selectedFile || error
            ? translate("buttonUploadImageTitle")
            : translate("buttonUploadAnotherImageTitle")}
        </ButtonLightenStyled>

        {error && <ErrorStyled>{error}</ErrorStyled>}
      </ComponentStyled>
    );
  }
);

export default React.memo(Component);
