import React, { useMemo } from "react";
import { PopupFiltersActionButtonsProps } from "./types";
import ContainerStyled from "./styles";
import { PopupFiltersActionButton as ButtonItem } from "./Item";

const Component: React.FC<PopupFiltersActionButtonsProps> = ({
  data,
  filtersFormikFormsData,
  ...restProps
}) => {
  const Buttons = useMemo(
    () =>
      data.map((itemData, _) => {
        const activeFiltersCount = Object.values(
          filtersFormikFormsData[itemData.filterName].values
        ).filter((i: boolean) => i).length;

        return (
          <li key={`${itemData.name ?? Date.now()}-${itemData.filterName}`}>
            <ButtonItem
              key={itemData.titleLangKey}
              {...itemData}
              activeFiltersCount={activeFiltersCount}
            />
          </li>
        );
      }),
    [data, filtersFormikFormsData]
  );

  return <ContainerStyled {...restProps}>{Buttons}</ContainerStyled>;
};

export default React.memo(Component);
