import React, { useMemo } from "react";
import SelectFieldProps from "./types";
import ContentStyled from "./styles";

export const defaultValue = "----";

const Component: React.FC<SelectFieldProps> = ({
  value,
  style,
  options,
  ...restProps
}) => {
  const finalValue =
    value !== undefined && value.toString().trim() ? value : defaultValue;

  const Options = useMemo(
    () =>
      [defaultValue, ...options].map((option, index) => (
        <option key={index} value={option}>
          {option}
        </option>
      )),
    [options]
  );

  return (
    <ContentStyled value={finalValue} {...restProps}>
      {Options}
    </ContentStyled>
  );
};

export default React.memo(Component);
