import React, { useMemo } from "react";
import { FilterSliderFieldProps } from "./types";
import ComponentStyled, {
  InputSliderTextContainerStyled,
  InputSliderTextFilterValueInfoStyled,
} from "./styles";
import { SliderField } from "../SliderField";

const Component: React.FC<FilterSliderFieldProps> = ({
  filterName,
  promptName,
  sliderInputData,
  onValueChange,
  ...restProps
}) => {
  const labelText = useMemo((): string => {
    let result = promptName;

    if (filterName === undefined || !filterName.trim()) {
      return result;
    }
    return `${filterName}:${result}`;
  }, [filterName, promptName]);

  return (
    <ComponentStyled {...restProps}>
      <InputSliderTextContainerStyled>
        <span>{labelText}</span>
      </InputSliderTextContainerStyled>

      <InputSliderTextFilterValueInfoStyled>
        <span>{sliderInputData.value}</span>
      </InputSliderTextFilterValueInfoStyled>

      <SliderField
        {...sliderInputData}
        onChange={({ x }) => onValueChange(+x.toString().slice(0, 3))}
      />
    </ComponentStyled>
  );
};

export default React.memo(Component);
