
import styled from 'styled-components';
import { ButtonProps } from './types';
import { buttonStyle } from '../../styles/common';

const greenBackgroundColor = '#17b169';
const greenBackgroundColorHover = '#146c43';
const blueBackgroundColor = '#0d6efd';
const blueBackgroundColorHover = '#0046b1';
const redBackgroundColor = '#dc3545';
const redBackgroundColorHover = '#a52834';

const generateBlueBtnStyles = (isDisabled: boolean) => `
  background-color: ${blueBackgroundColor};

  &:hover {
    background-color: ${!isDisabled ? blueBackgroundColorHover : blueBackgroundColor};
    border-color: #0d6efd;
  }
`;

const generateGreenBtnStyles = (isDisabled: boolean) => `
  background-color: ${greenBackgroundColor};

  &:hover {
    background-color: ${!isDisabled ? greenBackgroundColorHover : greenBackgroundColor};
    border-color: #17b169;
  }
`;

const generateRedBtnStyles = (isDisabled: boolean) => `
  background-color: ${redBackgroundColor};
  border-color: ${redBackgroundColor};

  &:hover {
    background-color: ${!isDisabled ? redBackgroundColor : redBackgroundColorHover};
  }
`;

export default styled.button<ButtonProps>`
  ${({withActiveStyles}) => buttonStyle(withActiveStyles)}
  ${({withBlueColor, disabled})  => withBlueColor ? generateBlueBtnStyles(disabled!!) : ''};
  ${({withGreenColor, disabled})  => withGreenColor ? generateGreenBtnStyles(disabled!!) : ''};
  ${({withRedColor, disabled})  => withRedColor ? generateRedBtnStyles(disabled!!) : ''};
  ${({disabled})  => `opacity: ${ !disabled ? 1 : .5}`};
  transition: opacity .15s ease-in-out;

  &[disabled] {
    background-color: rgba(0,88,221,.2) !important;
    color: rgba(13,110,253) !important;
    cursor: default !important;
  }
`;
 