import { useCallback, useEffect, useMemo, useState } from "react";
import { useFormik } from "formik";

import {
  ArtistsFiltersFormValues,
  CameraFiltersFormValues,
  ColorsFiltersFormValues,
  LightingFiltersFormValues,
  MaterialsFiltersFormValues,
  PopupFilterData,
  PopupFilterName,
  PopupFiltersData,
  PopupsFiltersFormikFormsData,
  PopupsFiltersFormInputValues,
  StylesFiltersFormValues,
  UsePopupFiltersProps,
} from "../types";
import {
  ARTISTS_FILTERS_DATA,
  CAMERA_FILTERS_DATA,
  COLORS_FILTERS_DATA,
  filterSomeDataItems,
  LIGHTING_FILTERS_DATA,
  MATERIALS_FILTERS_DATA,
  POPUPS_FILTERS_STATIC_DATA,
  STYLES_FILTERS_DATA,
} from "../utils";
import { PopupActiveStatusChange } from "../components";

export const usePopupFilters: UsePopupFiltersProps = () => {
  const [popupsFilters, setPopupsFilters] = useState<PopupFiltersData[]>([]);

  const defaultFormikFormsInitialValuesData = useMemo((): PopupFiltersData[] => POPUPS_FILTERS_STATIC_DATA.map((item, _) => ({
    ...item,
    isShow: false,
  })), []);

  useEffect(() => {
    setPopupsFilters(
      defaultFormikFormsInitialValuesData
    );
  }, [defaultFormikFormsInitialValuesData]);

  const handleFormikFormSubmit = useCallback(
    (
      currentPopupFiltersState: PopupFiltersData[],
      filterName: PopupFilterName,
      formValues: PopupsFiltersFormInputValues
    ) => {
      const activePopupsFilters: PopupFilterData[] = [];
      const selectedPromptsNames = Object.keys(formValues).filter(
        (filterName) => formValues[filterName]
      );

      for (let i = 0; i <= selectedPromptsNames.length - 1; i++) {
        const promptName = selectedPromptsNames[i];
        const somePopupsFiltersPromptInputSliderItem =
          currentPopupFiltersState.filter(
            (item) =>
              item.activePromptOptions.findIndex(
                (promptTagData) => promptTagData.promptName === promptName
              ) !== -1
          );
        const isSomePromptInputSliderExisting =
          !!somePopupsFiltersPromptInputSliderItem.length;

        if (isSomePromptInputSliderExisting) {
          continue;
        }
        activePopupsFilters.push({
          promptName: selectedPromptsNames[i],
          promptValue: 3,
        });
      }

      const popupsFiltersItemIndex = [...currentPopupFiltersState].findIndex(
        (item) => item.name === filterName
      );

      if (popupsFiltersItemIndex === -1) {
        return;
      }
      const newPopupsFiltersState = [...currentPopupFiltersState];
      const oldPopupsFiltersItemData =
        newPopupsFiltersState[popupsFiltersItemIndex];
      const actualOldPromptOptions =
        oldPopupsFiltersItemData.activePromptOptions.filter((item) =>
          selectedPromptsNames.includes(item.promptName)
        );

      newPopupsFiltersState[popupsFiltersItemIndex] = {
        ...newPopupsFiltersState[popupsFiltersItemIndex],
        activePromptOptions: filterSomeDataItems(
          actualOldPromptOptions,
          activePopupsFilters,
          "promptName"
        ),
      };
      setPopupsFilters(newPopupsFiltersState);
    },
    []
  );

  const stylesFormInitialValues = useMemo(
    (): StylesFiltersFormValues =>
      STYLES_FILTERS_DATA.items.reduce(
        (acc, item) => ({
          ...acc,
          [item.name]: false,
        }),
        {} as StylesFiltersFormValues
      ),
    []
  );

  const stylesFormFormikData = useFormik<StylesFiltersFormValues>({
    initialValues: stylesFormInitialValues,
    onSubmit: (values) =>
      handleFormikFormSubmit(popupsFilters, "Styles", values),
  });

  const popupLightingFormInitialValues = useMemo(
    (): LightingFiltersFormValues =>
      LIGHTING_FILTERS_DATA.items.reduce(
        (acc, item) => ({
          ...acc,
          [item.name]: false,
        }),
        {} as LightingFiltersFormValues
      ),
    []
  );

  const lightingFormFormikData = useFormik<LightingFiltersFormValues>({
    initialValues: popupLightingFormInitialValues,
    onSubmit: (values) =>
      handleFormikFormSubmit(popupsFilters, "Lighting", values),
  });

  const cameraFormInitialValues = useMemo(
    (): CameraFiltersFormValues =>
      CAMERA_FILTERS_DATA.items.reduce(
        (acc, item) => ({
          ...acc,
          [item.name]: false,
        }),
        {} as CameraFiltersFormValues
      ),
    []
  );

  const cameraFormFormikData = useFormik<CameraFiltersFormValues>({
    initialValues: cameraFormInitialValues,
    onSubmit: (values) =>
      handleFormikFormSubmit(popupsFilters, "Camera", values),
  });

  const artistsFormInitialValues = useMemo(
    (): ArtistsFiltersFormValues =>
      ARTISTS_FILTERS_DATA.items.reduce(
        (acc, item) => ({
          ...acc,
          [item.name]: false,
        }),
        {} as ArtistsFiltersFormValues
      ),
    []
  );

  const artistsFormFormikData = useFormik<CameraFiltersFormValues>({
    initialValues: artistsFormInitialValues,
    onSubmit: (values) =>
      handleFormikFormSubmit(popupsFilters, "Artists", values),
  });

  const colorsFormInitialValues = useMemo(
    (): ColorsFiltersFormValues =>
      COLORS_FILTERS_DATA.items.reduce(
        (acc, item) => ({
          ...acc,
          [item.name]: false,
        }),
        {} as ColorsFiltersFormValues
      ),
    []
  );

  const colorsFormFormikData = useFormik<ColorsFiltersFormValues>({
    initialValues: colorsFormInitialValues,
    onSubmit: (values) =>
      handleFormikFormSubmit(popupsFilters, "Colors", values),
  });

  const materialsFormInitialValues = useMemo(
    (): MaterialsFiltersFormValues =>
      MATERIALS_FILTERS_DATA.items.reduce(
        (acc, item) => ({
          ...acc,
          [item.name]: false,
        }),
        {} as MaterialsFiltersFormValues
      ),
    []
  );

  const materialsFormFormikData = useFormik<MaterialsFiltersFormValues>({
    initialValues: materialsFormInitialValues,
    onSubmit: (values) =>
      handleFormikFormSubmit(popupsFilters, "Materials", values),
  });

  const formsData = useMemo(
    (): PopupsFiltersFormikFormsData => ({
      Styles: stylesFormFormikData,
      Lighting: lightingFormFormikData,
      Camera: cameraFormFormikData,
      Artists: artistsFormFormikData,
      Colors: colorsFormFormikData,
      Materials: materialsFormFormikData,
    }),
    [
      artistsFormFormikData,
      cameraFormFormikData,
      colorsFormFormikData,
      lightingFormFormikData,
      materialsFormFormikData,
      stylesFormFormikData,
    ]
  );

  const handlePopupActiveStatusChange = useCallback<PopupActiveStatusChange>(
    (name, newStatus) => {
      const popupsFiltersItemIndex = popupsFilters.findIndex(
        (item) => item.name === name
      );
      let newPopupsFiltersData = [...popupsFilters];

      if (popupsFiltersItemIndex <= -1) {
        return;
      }
      newPopupsFiltersData[popupsFiltersItemIndex] = {
        ...newPopupsFiltersData[popupsFiltersItemIndex],
        isShow: newStatus,
      };

      setPopupsFilters(newPopupsFiltersData);
    },
    [popupsFilters]
  );

  const isAnyPopupShows = useMemo(
    (): boolean => !!popupsFilters.filter((item) => item.isShow).length,
    [popupsFilters]
  );

  return {
    data: popupsFilters,
    setData: setPopupsFilters,
    defaultFormikFormsInitialValuesData,
    formikFormsData: formsData,
    onPopupActiveStatusChange: handlePopupActiveStatusChange,
    isAnyPopupShows,
  };
};
