import React, { useMemo } from "react";
import { FiltersGridProps } from "./types";
import ComponentStyled, {
  ItemStyled,
  ItemImageStyled,
  ItemDescriptionStyled,
} from "./styles";
import { useLangStrings } from "../../hocs/useLangStrings";

const imageAltSuffix = "image";

const Component = <FormFormikProps extends Record<string, boolean>>({
  data,
  formik,
  ...restProps
}: FiltersGridProps<FormFormikProps>) => {
  const { translate } = useLangStrings();

  const Items = useMemo(
    (): JSX.Element[] =>
      data.items.map((item, index) => (
        <ItemStyled key={`${data.name}-${item.name}-${index}`}>
          <input
            id={item.name}
            name={item.name}
            type="checkbox"
            hidden
            checked={formik.values[item.name]}
            onChange={formik.handleChange}
          />

          <ItemImageStyled
            src={item.image}
            alt={`${item.name} ${imageAltSuffix}`}
          />

          <ItemDescriptionStyled>
            <span>{translate(item.langStringKey)}</span>
          </ItemDescriptionStyled>
        </ItemStyled>
      )),
    [data, formik, translate]
  );

  return <ComponentStyled {...restProps}>{Items}</ComponentStyled>;
};

export default React.memo(Component);
