import { useMemo } from "react";
import {
  FilterTextFieldName,
  PopupFilterData,
  UsePromptResult,
} from "../types";
import { trimValueMoreSpaces } from "../utils";
import {
  nativeFilterCrefTagPrompt,
  nativeFiltersFormikFormCwInputName,
} from "./useNativeFilters";
import { FormikValues } from "formik";
import { FilterTileFieldValues } from "../components/FilterFields/types";

export const promptIntro = "/imagine prompt: ";
export const defaultGenerationPromptResultInstructionPrefix = "--";
const defaultGenerationPromptResultInstructionSuffix = "::";

export const usePromptResult: UsePromptResult = ({
  myPromptTextValue,
  donorImageUrl,
  isUploadImageApprove,
  nativeFormikFormData,
  popupsFiltersData,
  generationPromptResultInstruction,
}) => {
  const nativeFormikFormFilterNames = useMemo((): FilterTextFieldName[] => {
    let result = [
      ...Object.keys(nativeFormikFormData.values),
    ] as FilterTextFieldName[];

    if (!isUploadImageApprove) {
      return result.filter(
        (promptTagName) => promptTagName !== nativeFiltersFormikFormCwInputName
      );
    }
    return result;
  }, [nativeFormikFormData, isUploadImageApprove]);

  const nativeFiltersPromptResult = useMemo((): string => {
    let result = "";
    const activeFilterFieldNames = nativeFormikFormFilterNames.filter(
      (promptName: string) =>
        !!(nativeFormikFormData.values as FormikValues)[promptName]
          .toString()
          .trim()
    );

    for (let i = 0; i <= activeFilterFieldNames.length - 1; i++) {
      const activePromptName = activeFilterFieldNames[i];
      const activePromptValue = (nativeFormikFormData.values as FormikValues)[
        activeFilterFieldNames[i]
      ];
      const isNumberPromptValue = !Number.isNaN(+activePromptValue);

      if (activePromptValue === FilterTileFieldValues.No.toString()) {
        continue;
      }
      if (activePromptValue === FilterTileFieldValues.Yes.toString()) {
        result += ` ${
          generationPromptResultInstruction?.prefix ??
          defaultGenerationPromptResultInstructionPrefix
        }${activePromptName}`;
        continue;
      }
      if (isNumberPromptValue && !+activePromptValue) {
        continue;
      }
      result += ` ${
        generationPromptResultInstruction?.prefix ??
        defaultGenerationPromptResultInstructionPrefix
      }${activePromptName} ${activePromptValue}`;
    }
    return result;
  }, [
    nativeFormikFormData,
    generationPromptResultInstruction,
    nativeFormikFormFilterNames,
  ]);

  const popupFiltersPromptResult = useMemo(() => {
    let result = "";
    const activePopupFilters = popupsFiltersData
      .filter((item) => !!item.activePromptOptions.length)
      .reduce(
        (result, activeItemFilters) => [
          ...result,
          ...activeItemFilters.activePromptOptions,
        ],
        [] as PopupFilterData[]
      );

    for (let i = 0; i <= activePopupFilters.length - 1; i++) {
      const activeFilters = activePopupFilters[i];

      result += ` ${
        generationPromptResultInstruction?.prefix ?? ""
      }${activeFilters.promptName.toLowerCase()}${
        generationPromptResultInstruction?.suffix ??
        defaultGenerationPromptResultInstructionSuffix
      }${activeFilters.promptValue}`;
    }
    return result;
  }, [generationPromptResultInstruction, popupsFiltersData]);

  const finalResult = useMemo((): string => {
    let finalPrompt = "";
    const myPromptValueWithoutManySpaces =
      trimValueMoreSpaces(myPromptTextValue);
    const finalMyPromptValue =
      `${myPromptValueWithoutManySpaces.trim()}`.replace(/[\r\n]+/g, " ");

    if (
      !isUploadImageApprove ||
      donorImageUrl === undefined ||
      !donorImageUrl.toString().trim()
    ) {
      finalPrompt = trimValueMoreSpaces(
        `${promptIntro} ${
          !finalMyPromptValue ? "" : `${finalMyPromptValue}`
        }${popupFiltersPromptResult}${nativeFiltersPromptResult}`
      )
        .replace(
          new RegExp(`--${nativeFiltersFormikFormCwInputName}\\s*\\d+`, "g"),
          ""
        )
        .trim();

      return finalPrompt;
    }
    finalPrompt = trimValueMoreSpaces(
      `${promptIntro} ${
        !finalMyPromptValue ? "" : `${finalMyPromptValue}`
      }${popupFiltersPromptResult} ${nativeFilterCrefTagPrompt} ${donorImageUrl}${nativeFiltersPromptResult}`
    ); 
    return finalPrompt;
  }, [
    donorImageUrl,
    isUploadImageApprove,
    myPromptTextValue,
    nativeFiltersPromptResult,
    popupFiltersPromptResult,
  ]);

  return {
    finalResult,
    nativeFiltersPromptResult,
    popupFiltersPromptResult,
  };
};
