import {
  useCallback,
  useEffect,
  useLayoutEffect,
  useRef,
  useState,
} from "react";
import styled from "styled-components";

import { ContainerLayout } from "./layouts";
import { useLangStrings, usePopupFilters } from "./hocs";
import { containerWhiteRoundedStyle } from "./styles/common";
import { SupportLang } from "./types/lang";
import {
  Button,
  ControllerButtons,
  FilterFields,
  TextField,
  PopupFiltersActionButtons,
  PopupsFilters,
  FilterSliders,
  UploadImage,
  OnFilterSlidersValueChange,
  FilterSlidersItemData,
  LanguageButtons,
  UploadImageRefProps,
} from "./components";
import {
  POPUP_FILTERS_ACTION_BUTTONS_STATIC_DATA,
  supportLangCodes,
} from "./utils";
import PopupFiltersActionButtonProps from "./components/PopupFiltersActionButtons/Item/types";
import {
  nativeFiltersFormikFormCwInputName,
  useNativeFilters,
} from "./hocs/useNativeFilters";
import { usePromptResult } from "./hocs/usePromptResult";
import { PopupFilterName } from "./types/common";

//const promptCustomEnd = "::";
const rootDomElementSelector = "#root";
const rootDomElementWithPopupAttribute = "with-any-popup";

function App() {
  const [myPrompt, setMyPrompt] = useState("");
  const [donorImageUrl, setDonorImageUrl] = useState("");
  const [popupFiltersActionButtons, setPopupFiltersActionButtons] = useState<
    PopupFiltersActionButtonProps[]
  >([]);
  const uploadImageRef = useRef<UploadImageRefProps>(null);

  const { selectedLang, setLanguage, translate } = useLangStrings();
  const {
    data: popupsFilters,
    setData: setPopupsFilters,
    formikFormsData: popupsFiltersFormsData,
    defaultFormikFormsInitialValuesData:
      defaultPopupsFiltersFormikFormsInitialValuesData,
    onPopupActiveStatusChange: handlePopupActiveStatusChange,
    isAnyPopupShows: isAnyPopupFiltersShows,
  } = usePopupFilters();

  const { formikFormData: nativeFormFormikData, isUploadImageApprove } =
    useNativeFilters({});

  const { finalResult: finalPromptValue } = usePromptResult({
    isUploadImageApprove,
    donorImageUrl,
    myPromptTextValue: myPrompt,
    nativeFormikFormData: nativeFormFormikData,
    popupFiltersFormikFormData: popupsFiltersFormsData,
    popupsFiltersData: popupsFilters,
  });

  useLayoutEffect(() => {
    const rootDomElement = document.querySelector(rootDomElementSelector);

    if (rootDomElement == null) {
      return;
    }
    rootDomElement.setAttribute(
      rootDomElementWithPopupAttribute,
      `${!!isAnyPopupFiltersShows}`
    );
  }, [isAnyPopupFiltersShows]);

  useEffect(() => {
    setPopupFiltersActionButtons(
      POPUP_FILTERS_ACTION_BUTTONS_STATIC_DATA.map((item, _) => {
        const popupDataIndex = popupsFilters.findIndex(
          (popupItem, _) => popupItem.name === item.filterName
        );

        if (popupDataIndex <= -1) {
          return item;
        }
        return {
          ...item,
          onClick: () =>
            setPopupsFilters((oldData) =>
              oldData.map((i, ii) =>
                ii !== popupDataIndex ? i : { ...i, isShow: true }
              )
            ),
        };
      })
    );
  }, [popupsFilters, setPopupsFilters]);

  const handleCustomPromptChangeValue = useCallback(
    (newVal: string = "") => setMyPrompt(newVal),
    []
  );

  const handleLanguageButtonClick = useCallback(
    (newLang: SupportLang) => {
      if (newLang === selectedLang) {
        return;
      }
      setLanguage(newLang);
    },
    [selectedLang, setLanguage]
  );

  const handleFilterPromptTagSliderValueChange =
    useCallback<OnFilterSlidersValueChange>(
      ({ filterName, promptName, newValue }) => {
        const oldPopupsFiltersItemDataIndex = popupsFilters.findIndex(
          (item) => item.name === filterName
        );

        if (oldPopupsFiltersItemDataIndex <= -1) {
          return;
        }
        const oldPopupsFiltersItemData =
          popupsFilters[oldPopupsFiltersItemDataIndex];
        const oldPopupsFiltersPromptSliderDataIndex =
          oldPopupsFiltersItemData.activePromptOptions.findIndex(
            (item) => item.promptName === promptName
          );
        const newPupsFiltersPromptSlidersData = [
          ...oldPopupsFiltersItemData.activePromptOptions,
        ];

        if (oldPopupsFiltersPromptSliderDataIndex <= -1) {
          return;
        }
        const newPopupsFiltersData = [...popupsFilters];
        const newPopupsFiltersPromptSliderItemData =
          newPupsFiltersPromptSlidersData[
            oldPopupsFiltersPromptSliderDataIndex
          ];

        newPupsFiltersPromptSlidersData[oldPopupsFiltersPromptSliderDataIndex] =
          {
            ...newPopupsFiltersPromptSliderItemData,
            promptValue: newValue,
          };
        newPopupsFiltersData[oldPopupsFiltersItemDataIndex] = {
          ...oldPopupsFiltersItemData,
          activePromptOptions: newPupsFiltersPromptSlidersData,
        };

        setPopupsFilters(newPopupsFiltersData);
      },
      [popupsFilters, setPopupsFilters]
    );

  const handleUploadImageFilterSliderValueChange = useCallback(
    (newValue: number) =>
      nativeFormFormikData.setFieldValue(
        nativeFiltersFormikFormCwInputName,
        newValue
      ),
    [nativeFormFormikData]
  );

  const generateFilterSlidersDataItems =
    useCallback((): FilterSlidersItemData[] => {
      let data: FilterSlidersItemData[] = [];

      for (let i = 0; i <= popupsFilters.length - 1; i++) {
        const filtersData = popupsFilters[i];
        const filterName = popupsFilters[i].name;

        if (!filtersData.activePromptOptions.length) {
          continue;
        }
        for (
          let ii = 0;
          ii <= filtersData.activePromptOptions.length - 1;
          ii++
        ) {
          const filterPromptData = filtersData.activePromptOptions[ii];
          const promptName = filterPromptData.promptName;

          data.push({
            filterName,
            promptName,
            inputData: {
              value: filterPromptData.promptValue,
            },
          });
        }
      }

      return data;
    }, [popupsFilters]);

  const handleClearAllClick = useCallback(() => {
    setMyPrompt("");
    nativeFormFormikData.resetForm();
    Object.keys(popupsFiltersFormsData).forEach((formDataName) =>
      popupsFiltersFormsData[formDataName as PopupFilterName].resetForm()
    );
    setPopupsFilters(defaultPopupsFiltersFormikFormsInitialValuesData);

    uploadImageRef.current?.clearInputAndState();
    setDonorImageUrl("");
  }, [
    nativeFormFormikData,
    popupsFiltersFormsData,
    setPopupsFilters,
    defaultPopupsFiltersFormikFormsInitialValuesData,
  ]);

  return (
    <div className="App">
      <header>
        <script src="https://telegram.org/js/telegram-web-app.js"></script>
      </header>

      <ContainerLayout>
        <Head>
          {/*<HeadTitle>{translate("title")}</HeadTitle>*/}

          <LanguageButtons
            data={[...supportLangCodes]}
            activeSupportLang={selectedLang}
            onItemClick={handleLanguageButtonClick}
          />
        </Head>

        <TextareaStyled
          cols={40}
          rows={1}
          isTextarea
          withTextareaAutoHeight
          value={myPrompt}
          onChange={(e) => handleCustomPromptChangeValue(e.target.value)}
          placeholder={translate("inputPromptPlaceholder")}
        />
        <TextareaWithSmallMTStyled
          cols={40}
          rows={1}
          isTextarea
          withTextareaAutoHeight
          readOnly
          value={finalPromptValue}
          placeholder={translate("inputPromptPlaceholder")}
        />

        <ControllerButtonsStyled copyTextValue={finalPromptValue} />

        <WhiteContainerRounded>
          <FilterFields formikFormData={nativeFormFormikData} />

          <PopupFiltersActionButtonsStyled
            filtersFormikFormsData={popupsFiltersFormsData}
            data={popupFiltersActionButtons}
          />

          <FilterSlidersStyled
            items={generateFilterSlidersDataItems()}
            onItemValueChange={handleFilterPromptTagSliderValueChange}
          />

          {isUploadImageApprove && (
            <UploadImageStyled
              ref={uploadImageRef}
              formikFormData={nativeFormFormikData}
              imageUrl={donorImageUrl}
              setUploadedImageUrl={setDonorImageUrl}
              onFilterSliderValueChange={
                handleUploadImageFilterSliderValueChange
              }
            />
          )}

          <ClearButtonButtonStyled withBlueColor onClick={handleClearAllClick}>
            {translate("buttonClearFiltersTitle")}
          </ClearButtonButtonStyled>
        </WhiteContainerRounded>
      </ContainerLayout>

      <PopupsFilters
        formikFormsData={popupsFiltersFormsData}
        data={popupsFilters}
        onPopupActiveStatusChange={handlePopupActiveStatusChange}
      />
    </div>
  );
}

const Head = styled.div`
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  @media (max-width: 600px) {
    justify-content: center;
  }
`;
/*const HeadTitle = styled.h1`
  display: inline-block;
  font-size: 40px;
  font-weight: 500;

  @media (max-width: 1000px) {
    font-size: 25px;
  }

  @media (max-width: 768px) {
    font-size: 20px;
  }

  @media (max-width: 600px) {
    margin-top: 15px;
  }
`;*/

const ControllerButtonsStyled = styled(ControllerButtons)`
  width: 100%;
  margin-top: 15px;
`;

const TextareaStyled = styled(TextField)`
  margin-top: 20px;
`;

const TextareaWithSmallMTStyled = styled(TextField)`
  margin-top: 10px;
`;

const WhiteContainerRounded = styled.div`
  margin-top: 15px;
  ${containerWhiteRoundedStyle()};

  @media (max-width: 530px) {
    padding: 16px 20px;
  }
`;

const FilterSlidersStyled = styled(FilterSliders)`
  margin-top: 10px;
`;

const PopupFiltersActionButtonsStyled = styled(PopupFiltersActionButtons)`
  margin-top: 25px;
`;

const UploadImageStyled = styled(UploadImage)`
  margin-top: 25px;
`;

const ClearButtonButtonStyled = styled(Button)`
  margin-top: 20px;
`;

export default App;
