import React from "react";
import Props from "./types";
import { useLangStrings } from "../../../hocs/useLangStrings";
import { FiltersCountStyled, ButtonStyled } from "./styles";

const Component: React.FC<Props> = ({
  titleLangKey,
  iconFontAwesomeClassName,
  activeFiltersCount,
  onClick,
  ...restProps
}) => {
  const { translate } = useLangStrings();

  const isStylesFiltersActive =
    activeFiltersCount !== undefined && activeFiltersCount >= 1;

  return (
    <ButtonStyled
      isLarge={isStylesFiltersActive}
      onClick={onClick}
      {...restProps}
    >
      <i className={iconFontAwesomeClassName} /> {translate(titleLangKey)}
      {isStylesFiltersActive && (
        <FiltersCountStyled>{activeFiltersCount}</FiltersCountStyled>
      )}
    </ButtonStyled>
  );
};

export default React.memo(Component);
