import React, { useCallback } from "react";
import { PopUpLayoutProps } from "./types";
import ContainerStyled, {
  ContentStyled,
  ContentHeadStyled,
  TitleStyled,
  FooterStyled,
  BackdropButtonStyled,
  ContentHeadCloseStyled,
  ContentBodyStyled,
} from "./styles";

const Component: React.FC<PopUpLayoutProps> = ({
  title,
  isActive,
  setIsActive,
  FooterComponents,
  onClose,
  children,
  ...restProps
}) => {
  const handleCloseClick = useCallback(() => {
    setIsActive(false);

    onClose?.();
  }, [onClose, setIsActive]);

  return (
    <ContainerStyled isActive={isActive} {...restProps}>
      <BackdropButtonStyled onClick={handleCloseClick} />

      <ContentStyled>
        <ContentHeadStyled>
          <TitleStyled>{title}</TitleStyled>

          <ContentHeadCloseStyled onClick={handleCloseClick} />
        </ContentHeadStyled>

        <ContentBodyStyled>{children}</ContentBodyStyled>

        <FooterStyled>{FooterComponents}</FooterStyled>
      </ContentStyled>
    </ContainerStyled>
  );
};

export default React.memo(Component);
