import React, { useMemo } from "react";
import { FilterSlidersProps } from "./types";
import ComponentStyled from "./styles";
import { FilterSliderField } from "../FilterSliderField";

const Component: React.FC<FilterSlidersProps> = ({
  items,
  onItemValueChange,
  ...restProps
}) => {
  const InputSliders = useMemo(
    (): React.ReactNode[] =>
      items.map(({ filterName, promptName, inputData }, _) => {
        const handleValueChange = (newValue: number) =>
          onItemValueChange({ filterName, promptName, newValue });

        return (
          <FilterSliderField
            key={`${filterName}-${promptName}`}
            filterName={filterName}
            promptName={promptName}
            sliderInputData={inputData}
            onValueChange={handleValueChange}
          />
        );
      }),
    [items, onItemValueChange]
  );

  return <ComponentStyled {...restProps}>{InputSliders}</ComponentStyled>;
};

export default React.memo(Component);
