import React from "react";
import { Tooltip } from "react-tooltip";
import { CopyToClipboard } from "react-copy-to-clipboard";

import { ControllerButtonsProps } from "./types";
import ContainerStyled from "./styles";
import { Button } from "../Button";
import { useLangStrings } from "../../hocs";

const Component: React.FC<ControllerButtonsProps> = ({
  copyTextValue,
  onCopyBtnClick,
  onSaveBtnClick,
  ...restProps
}) => {
  const { translate } = useLangStrings();

  return (
    <ContainerStyled {...restProps}>
      <span className="App-tooltip" id="tooltip-copy">
        <CopyToClipboard text={copyTextValue}>
          <Button withBlueColor withActiveStyles onClick={onCopyBtnClick}>
            {translate("buttonPromptCopyTitle")}
          </Button>
        </CopyToClipboard>

        <Tooltip anchorSelect="#tooltip-copy" className="App-tooltip-content">
          {translate("buttonPromptCopyDescription")}
        </Tooltip>
      </span>

      {/*<Button withGreenColor onClick={onSaveBtnClick}>
        {translate("buttonPromptSaveTitle")}
      </Button>*/}
    </ContainerStyled>
  );
};

export default React.memo(Component);
