import React, { useMemo } from "react";
import { LanguageButtonsProps } from "./types";
import ComponentStyled, {
  LanContainerItemStyled,
  LangButtonStyled,
} from "./styles";

const Component: React.FC<LanguageButtonsProps> = ({
  activeSupportLang,
  data,
  onItemClick,
  ...restProps
}) => {
  const Items = useMemo(
    (): React.ReactNode =>
      data.map((langCode) => (
        <LanContainerItemStyled
          isActive={langCode.toLowerCase() === activeSupportLang.toLowerCase()}
          key={`languages-item-${langCode}`}
        >
          <LangButtonStyled onClick={() => onItemClick(langCode)}>
            {langCode.toUpperCase()}
          </LangButtonStyled>
        </LanContainerItemStyled>
      )),
    [data, activeSupportLang, onItemClick]
  );

  return <ComponentStyled {...restProps}>{Items}</ComponentStyled>;
};

export default React.memo(Component);
