import React, { useMemo } from "react";
import { FilterFieldsProps, FilterTileFieldValues } from "./types";
import ContainerStyled, { ContainerBoxStyled } from "./styles";
import {
  FilterTextField,
  FilterSelectField,
  FilterSelectFieldProps,
  FieldProps,
} from "..";
import { AnyNativeFilterFormField } from "../../types";
import { NATIVE_FILTERS_STATIC_DATA } from "../../utils";

export const defaultNativeFilterTileSelectValue: FilterTileFieldValues =
  FilterTileFieldValues.No;
export const fieldItemUniqueKeyPrefix = "native-filter";

const Component: React.FC<FilterFieldsProps> = ({
  formikFormData,
  ...restProps
}) => {
  const finalItemsData = useMemo(
    (): AnyNativeFilterFormField[] =>
      NATIVE_FILTERS_STATIC_DATA.map((item) => ({
        ...item,
        value: !!item?.name ? formikFormData.values[item.name] : "",
        onChange: (newValue: any) => {
          if (!item?.name) {
            return;
          }
          formikFormData.setFieldValue(item.name, newValue.toString());
        },
        allFilterValues: formikFormData.values,
      })),
    [formikFormData]
  );

  const Fields = useMemo(
    () =>
      finalItemsData.map((inputData) => {
        const itemKey = `${fieldItemUniqueKeyPrefix}-${inputData.name}`;

        if (!!inputData?.options) {
          const itemData = { ...inputData } as FilterSelectFieldProps;

          return <FilterSelectField key={itemKey} {...itemData} />;
        }
        const itemData = { ...inputData } as FieldProps;

        return <FilterTextField key={itemKey} {...itemData} />;
      }),
    [finalItemsData]
  );

  return (
    <ContainerStyled {...restProps}>
      <ContainerBoxStyled>{Fields}</ContainerBoxStyled>
    </ContainerStyled>
  );
};

export default React.memo(Component);
