import React, { useEffect, useRef } from "react";
import { TextAreaFieldProps, TextInputFieldProps } from "./types";
import ContentStyled, { TextAreaStyled } from "./styles";

const Component: React.FC<TextAreaFieldProps | TextInputFieldProps> = (
  props
) => {
  const {
    isTextarea = false,
    withTextareaAutoHeight = false,
    value,
    ...restProps
  } = props;

  const textareaRef = useRef<HTMLTextAreaElement | null>(null);

  useEffect(() => {
    if (!isTextarea) {
      return;
    }
    const textarea = textareaRef.current;
    if (!withTextareaAutoHeight) {
      return;
    }
    const resizeTextarea = () => {
      if (!textarea) {
        return;
      }
      textarea.style.overflow = "hidden";
      textarea.style.height = "auto";
      textarea.style.height = `${textarea.scrollHeight}px`;
    };

    if (!textarea) {
      return;
    }
    textarea.addEventListener("input", resizeTextarea);
    resizeTextarea();

    return () => {
      textarea.removeEventListener("input", resizeTextarea);
    };
  }, [isTextarea, withTextareaAutoHeight, value]);

  if (isTextarea) {
    return (
      <TextAreaStyled
        ref={textareaRef}
        value={value}
        {...(restProps as TextAreaFieldProps)}
      />
    );
  }
  return (
    <ContentStyled value={value} {...(restProps as TextInputFieldProps)} />
  );
};

export default React.memo(Component);
