import React, { useCallback, useMemo } from "react";
import { FieldProps } from "./types";
import { useLangStrings } from "../../hocs/useLangStrings";
import { FilterInputNameValues } from "../FilterFields/types";
import { FilterInputLayout } from "../../layouts/FilterInputLayout";
import { FieldStyled } from "../../styles/common";
import { FilterTextFieldName, FilterTextFieldValue } from "../../types";
import { TextAreaFieldProps, TextInputFieldProps } from "../TextField";
import { trimValueMoreSpaces } from "../../utils";

const Component: React.FC<FieldProps> = ({
  readOnly = false,
  allFilterValues,
  setAllFilterValue,
  label,
  placeholder,
  name,
  tooltipDescription,
  faIconClassName,
  value,
  onChange,
  type,
  min,
  max,
  style,
  isTextarea = false,
  ...restProps
}) => {
  const { translate } = useLangStrings();

  const title = placeholder ? translate(placeholder) : "";

  const isInitValueExist = useMemo(
    (): boolean =>
      allFilterValues !== undefined &&
      name !== undefined &&
      allFilterValues[name] !== undefined,
    [allFilterValues, name]
  );

  const checkNumberValueValid = useCallback(
    (value: string | number) => !Number.isNaN(value),
    []
  );

  const validateValue = useCallback(
    (v: FilterTextFieldValue): FilterTextFieldValue => {
      if (type !== "number" || !v.toString().trim()) {
        return v;
      }
      const minNumber = Number.parseInt(min?.toString?.() ?? "-");
      const isMinValid = !Number.isNaN(minNumber);

      const maxNumber = Number.parseInt(max?.toString?.() ?? "-");
      const isMaxValid = !Number.isNaN(maxNumber);

      const valueNumber = Number.parseInt(v.toString().trim());
      const isValueValid = checkNumberValueValid(valueNumber);

      if (!isValueValid) {
        return minNumber;
      }
      if (isMinValid && isValueValid && valueNumber < minNumber) {
        return minNumber;
      }
      if (isMaxValid && isValueValid && valueNumber > maxNumber) {
        return maxNumber;
      }
      return v;
    },
    [type, min, max, checkNumberValueValid]
  );

  const handleChangeValue = useCallback(
    (e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
      const text = e.target.value;
      const newValue = trimValueMoreSpaces(validateValue(text).toString());
      const isNumberValueValid = checkNumberValueValid(text.trim());

      e.preventDefault();
      e.stopPropagation();

      onChange?.(newValue);
      if (type === "number" && !isNumberValueValid) {
        return;
      }
      if (setAllFilterValue === undefined || name === undefined) {
        return;
      }
      setAllFilterValue({
        name: name,
        value: newValue,
      });
    },
    [
      type,
      onChange,
      setAllFilterValue,
      name,
      validateValue,
      checkNumberValueValid,
    ]
  );

  const finalValue = useMemo((): FilterTextFieldValue => {
    if (!isInitValueExist) {
      return value ?? "";
    }
    return (allFilterValues as FilterInputNameValues)[
      name as FilterTextFieldName
    ];
  }, [isInitValueExist, allFilterValues, value, name]);

  const commonFieldsProps = useMemo(
    () => ({
      min,
      max,
      placeholder: title,
      title,
      value: finalValue,
      type,
      readOnly,
    }),
    [finalValue, max, min, readOnly, title, type]
  );

  return (
    <FilterInputLayout
      style={style}
      className={faIconClassName}
      label={label}
      name={name}
      tooltipDescription={tooltipDescription}
      faIconClassName={faIconClassName}
    >
      {isTextarea ? (
        <FieldStyled
          {...(restProps as TextAreaFieldProps)}
          {...commonFieldsProps}
          onChange={handleChangeValue}
          isTextarea
        />
      ) : (
        <FieldStyled
          {...(restProps as TextInputFieldProps)}
          {...commonFieldsProps}
          onChange={handleChangeValue}
          isTextarea={false}
        />
      )}
    </FilterInputLayout>
  );
};

export default React.memo(Component);
