export class AppLang {
  private selectedLang: SupportLang = defaultLangCode;

  get selectedLanguage(): SupportLang {
    return this.selectedLang;
  }

  set selectedLanguage(newLang: SupportLang) {
    this.selectedLang = newLang;
  }

  strings: AppStringsType = appStrings;
  static supportLangs: readonly SupportLang[] = supportLangCodes;

  translate(stringValue: LangStringKey): LangStringKey | string {
    if (!stringValue.trim()) {
      return "";
    }

    const result = this.strings[this.selectedLang]?.[stringValue];
    return result ?? "";
  }
}
