import React, { createContext, useState, ReactNode, useCallback } from "react";
import { AppLang } from "../utils/lang";
import { LangStringKey, SupportLang } from "../types/lang";

export const UtilsLang = new AppLang();

export const LangContext = createContext<
  | {
      selectedLang: SupportLang;
      setLanguage: (lang: SupportLang) => void;
      translate: (key: LangStringKey) => string;
    }
  | undefined
>(undefined);

export const LangProvider: React.FC<{ children: ReactNode }> = ({
  children,
}) => {
  const [selectedLang, setSelectedLang] = useState<SupportLang>(
    UtilsLang.selectedLanguage
  );

  const setLanguage = useCallback(
    (lang: SupportLang) => {
      if (selectedLang === lang) {
        return;
      }
      setSelectedLang(lang);
    },
    [selectedLang]
  );

  const translate = (key: LangStringKey): string => {
    return UtilsLang.strings[selectedLang][key] ?? "";
  };

  return (
    <LangContext.Provider value={{ selectedLang, setLanguage, translate }}>
      {children}
    </LangContext.Provider>
  );
};
